<template>
  <div :class="{ active: category === activeItem ? true : false }" class="news-panel-item">
    <news-list-item :article="article" v-for="article in articles" :key="article._id" @click.native="jump(article._id)"/>
  </div>
</template>

<script>
  import NewsListItem from './NewsListItem'

  export default {
    data() {
      return {
        articles: []
      }
    },
    props: ['category', 'activeItem'],
    methods: {
      // 根据父组件 NewsList 传入的 category 获取相应的文章数据
      async getList() {
        const res = await this.$http.get('/web/api/article', {
          params: { category: this.category }
        })
        this.articles = res.data
      },
      jump(id) {
        this.$router.push(`/news/${id}`)
      }
    },
    components: { NewsListItem },
    created() {
      this.getList()
    }
  }
</script>

<style lang="scss" scoped>
  .news-panel-item {
    display: none;
    padding-top: 5px;

    &.active {
      display: block;
    }
  }
</style>