<template>
  <div class="faction-logo">
    <img :src="factionUrl()" />
  </div>
</template>

<script>
  import factionBlackSteel from '../../assets/images/factions/faction_black_steel.png'
  import factionLongmen from '../../assets/images/factions/faction_longmen.png'
  import factionPenguin from '../../assets/images/factions/faction_penguin.png'
  import factionRhine from '../../assets/images/factions/faction_rhine.png'
  import factionRhodesIsland from '../../assets/images/factions/faction_rhodes_island.png'

  export default {
    data() {
      return {
        faction: '企鹅物流'
      }
    },
    methods: {
      factionUrl() {
        switch (this.faction) {
          case '龙门':
            return factionLongmen
            break
          case '黑钢':
            return factionBlackSteel
            break
          case '企鹅物流':
            return factionPenguin
            break
          case '莱茵生命':
            return factionRhine
            break
          case '罗德岛':
            return factionRhodesIsland
            break
        }
      }
    }
  }
</script>

<style lang="scss" scope>
  .faction-logo {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
    transition: 0.3s;

    img {
      width: 100%;
    }
  }
</style>