<template>
  <div class="slide">
    <div class="char-tachie">
      <img :src="$store.state.baseUrl + character.tachie" />
    </div>
    <div class="char-attr-box">
      <div class="char-cv">{{ character.cv }}</div>
      <div class="char-eng">{{ character.engName }}</div>
      <div class="char-name">
        <img :src="vocationUrl" />
        {{ character.name }}
      </div>
    </div>
    <div class="desc-decoration"></div>
    <div class="char-desc">{{ character.description }}</div>
  </div>
</template>

<script>
  import vocationJinwei from '../../assets/images/vocations/vocation_jinwei.png'
  import vocationJuji from '../../assets/images/vocations/vocation_juji.png'
  import vocationShushi from '../../assets/images/vocations/vocation_shushi.png'
  import vocationTezhong from '../../assets/images/vocations/vocation_tezhong.png'
  import vocationXianfeng from '../../assets/images/vocations/vocation_xianfeng.png'
  import vocationYiliao from '../../assets/images/vocations/vocation_yiliao.png'
  import vocationZhongzhuang from '../../assets/images/vocations/vocation_zhongzhuang.png'

  export default {
    props: ['character'],
    computed: {
      vocationUrl() {
        switch (this.character.vocation) {
          case '近卫':
            return vocationJinwei
            break
          case '狙击':
            return vocationJuji
            break
          case '术士':
            return vocationShushi
            break
          case '特种':
            return vocationTezhong
            break
          case '先锋':
            return vocationXianfeng
            break
          case '医疗':
            return vocationYiliao
            break
          case '重装':
            return vocationZhongzhuang
            break
          default:
            break
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  .slide {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .char-tachie {
      position: absolute;
      z-index: -1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow: hidden;

      img {
        margin-left: -60%;
        height: 100%;
      }
    }

    .char-attr-box {
      padding-right: 20px;
      text-align: right;
      color: #fff;

      .char-cv {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-weight: bold;
        font-size: 18px;

        &::before {
          content: 'CV';
          display: inline-block;
          margin-right: 5px;
          padding: 0 3px;
          width: 30px;
          line-height: 18px;
          font-size: 14px;
          background-color: #0096d9;
        }
      }

      .char-eng {
        font-weight: bold;
        font-size: 26px;
      }

      .char-name {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-weight: bold;
        font-size: 46px;

        img {
          margin-right: 5px;
          height: 46px;
        }
      }
    }

    .desc-decoration {
      position: relative;
      margin: 10px 20px 0;
      height: 1px;
      background-color: #535353;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: -1px;
        width: 30px;
        height: 3px;
        background-color: #fff;
      }
    }

    .char-desc {
      padding: 10px 20px;
      height: 100px;
      color: #aaa;
      font-size: 14px;
    }
  }
</style>