<template>
  <div>
    <full-page :options="options" id="fullpage" ref="fullpage">
      <index />
      <news />
      <character :activeIndex="activeCharacterIndex" :reference="$refs" />
      <story :reference="$refs" />
    </full-page>
  </div>
</template>

<script>
  import BtnNav from '../components/BtnNav'
  import Index from '../views/Index'
  import News from '../views/News'
  import Character from '../views/Character'
  import Story from '../views/Story'

  export default {
    name: 'Home',
    data() {
      return {
        activeCharacterIndex: 0,
        options: {
          verticalCentered: false, // 内容是否垂直居中
          paddingTop: '50px', // 每个页面的 paddingTop
          scrollingSpeed: 500,
          afterSlideLoad: (section, origin, destination, direction) => {
            this.activeCharacterIndex = destination.index
          }
        }
      }
    },
    components: { BtnNav, Index, News, Character, Story }
  }
</script>
