<template>
  <div class="news-list-item">
    <div class="news-header">
      <span class="news-date">{{ formatDate }}</span>
      <i class="iconfont icon-more"></i>
    </div>
    <div class="news-body">
      <div class="news-tag">{{ article.category }}</div>
      <span class="news-title">{{ article.title }}</span>
    </div>
  </div>
</template>

<script>
  import moment from 'moment'

  export default {
    computed: {
      formatDate() {
        return moment(this.article.date).format('YYYY-MM-DD')
      }
    },
    props: ['article']
  }
</script>

<style lang="scss" scoped>
  .news-list-item {
    margin-top: -1px;

    .news-header {
      display: flex;
      justify-content: space-between;
      border: 1px solid #535353;
      height: 15px;

      .news-date {
        line-height: 15px;
        padding: 0 5px;
        background-color: #ffee22;
      }

      .icon-more {
        margin-right: 10px;
        line-height: 15px;
        color: #ffee22;
      }
    }

    .news-body {
      display: flex;
      border: 1px solid #535353;
      border-top: none;
      padding: 10px;
      height: 25px;
      line-height: 25px;
      font-size: 17px;

      .news-tag {
        width: 50px;
        font-weight: bold;
        color: #22bbff;
      }

      .news-title {
        flex-grow: 1;
        font-size: 16px;
        color: #fff;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
</style>