<template>
  <div id="newsDetail">
    <btn-nav></btn-nav>
    <div class="news-main">
      <div class="news-header">
        <h2 class="news-title">{{ article.title }}</h2>
        <div class="news-date">
          发布于
          <span>{{ formatDate }}</span>
        </div>
      </div>
      <div class="news-decoration"></div>
      <div class="news-content" v-html="article.content"></div>
    </div>
  </div>
</template>

<script>
  import moment from 'moment'
  import BtnNav from '../components/BtnNav'

  export default {
    data() {
      return {
        article: {}
      }
    },
    computed: {
      formatDate() {
        return moment(this.article.date).format('YYYY-MM-DD')
      }
    },
    methods: {
      async getArticle() {
        const res = await this.$http.get(`/web/api/article/${this.$route.params.id}`)
        this.article = res.data
      }
    },
    components: { BtnNav },
    created() {
      this.getArticle()
    }
  }
</script>

<style lang="scss">
  #newsDetail {
    min-height: 100vh;
    padding: 60px 20px 0;
    background-color: #131313;

    .news-main {
      padding: 30px 10px;
      color: #535353;
      background-color: #1c1c1c;

      .news-header {
        .news-title {
          color: #fff;
        }

        .news-date {
          text-align: right;
          color: #535353;

          span {
            color: #22bbff;
          }
        }
      }

      .news-decoration {
        position: relative;
        margin-top: 10px;
        height: 1px;
        background-color: #535353;

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: -1px;
          width: 30px;
          height: 3px;
          background-color: #fff;
        }
      }

      .news-content {
        padding-top: 30px;
        color: #ccc;

        img {
          width: 100%;
        }
      }
    }
  }
</style>