import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import NewsDetail from '../views/NewsDetail.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/news/:id',
        name: 'NewsDetail',
        component: NewsDetail
    },
]

const router = new VueRouter({
    routes
})

export default router
