<template>
  <div class="section story">
    <div :key="story._id" class="slide story-item" v-for="story in stories">
      <div class="story-pic">
        <div class="story-frame"></div>
        <img :src="$store.state.baseUrl + story.pic" />
      </div>
      <h2 class="story-title">{{ story.title }}</h2>
      <h3 class="story-sub-title">{{ story.engName }}</h3>
      <div class="desc-decoration"></div>
      <div class="story-desc">{{ story.description }}</div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        stories: []
      }
    },
    props: ['reference'],
    methods: {
      async getStories() {
        const res = await this.$http.get('/web/api/story')
        this.stories = res.data
      }
    },
    created() {
      this.getStories()
    },
    updated() {
      // 数据更新后调用 fullpage 的方法刷新 DOM，
      // 否则由于页面的 slide 还没加载完，fullpage 获取不到 DOM
      // 并且这里不能使用 this.$parent，因为嵌套组件只能获取到 fullpage 的 DOM，而不是组件实例，因此没法调用方法
      this.reference.fullpage.build()
    }
  }
</script>

<style lang="scss">
  .story {
    position: relative;
    background: url('../assets/images/character_bg.jpg') no-repeat bottom / cover;

    .fp-controlArrow.fp-prev {
      border-width: 10px 20px 10px 0 !important;
    }

    .fp-controlArrow.fp-next {
      border-width: 10px 0 10px 20px !important;
    }

    .story-item {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: column;

      .story-pic {
        position: relative;
        width: 320px;
        height: 320px;

        .story-frame {
          position: absolute;
          z-index: -1;
          left: 50%;
          top: 50%;
          width: 130px;
          height: 130px;
          border: 10px solid #fff;
          transform: translate(-50%, -50%);
          box-shadow: 0 10px 10px #000;
        }

        img {
          width: 100%;
        }
      }
    }

    .story-title {
      color: #fff;
      font-size: 42px;
    }

    .story-sub-title {
      color: #fff;
      font-size: 22px;
    }

    .desc-decoration {
      position: relative;
      margin: 10px 20px 0;
      width: 90%;
      height: 1px;
      background-color: #535353;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: -1px;
        width: 30px;
        height: 3px;
        background-color: #fff;
      }
    }

    .story-desc {
      padding: 10px 20px;
      height: 100px;
      color: #aaa;
      font-size: 14px;
    }
  }
</style>